import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Header.css";

export const Header = () => {
  const [mobileDisplayed, setMobileDisplayed] = useState(false);

  const handleClick = () => {
    setMobileDisplayed(!mobileDisplayed);
  };

  return (
    <header className="header">
      <h1 className="logo">T+C</h1>

      <ul className="navBar">
        <li>
          <Link className="item" to="/">
            Home
          </Link>
        </li>
        <li>
          <Link className="item" to="/about">
            About Us
          </Link>
        </li>
        <li>
          <Link className="item" to="/contact">
            Our Service
          </Link>
        </li>
      </ul>

      <button
        className={!mobileDisplayed ? "navBarMobile" : "navBarMobile rotate"}
        onClick={handleClick}
      >
        <li
          className="line"
          id={!mobileDisplayed ? "line1" : "line-1-rotate"}
        ></li>
        <li
          className="line"
          id={!mobileDisplayed ? "line2" : "line-2-rotate"}
        ></li>
        <li
          className="line"
          id={!mobileDisplayed ? "line3" : "line-3-rotate"}
        ></li>
      </button>
      <ul
        className={
          !mobileDisplayed
            ? "mobileMenu active-mobileMenu"
            : "mobileMenu no-active-mobileMenu"
        }
      >
        <li>
          <Link className="link-mobile" to="/" onClick={handleClick}>
            Home
          </Link>
        </li>
        <li>
          <Link className="link-mobile" to="/about" onClick={handleClick}>
            About Us
          </Link>
        </li>
        <li>
          <Link className="link-mobile" to="/contact" onClick={handleClick}>
            Our Service
          </Link>
        </li>
      </ul>
    </header>
  );
};
