import React from "react";
import "./Contact.css";

export const Contact = () => {
  return (
    <>
      <section className="form--container">
        <div className="form--inner--container">
          <h1>Send us your information and will contact you:</h1>
          <form>
            <label htmlFor="fname" className="input-text">
              First name:
            </label>
            <br />
            <input type="text" className="input" id="fname" name="fname" />
            <br />
            <label htmlFor="lname" className="input-text">
              Last name:
            </label>
            <br />
            <input type="text" className="input" id="lname" name="lname" />
            <br />
            <label htmlFor="Pname" className="input-text">
              Phone Number:
            </label>
            <br />
            <input type="text" className="input" id="Pname" name="Pname" />
            <br />
            <label htmlFor="Ename" className="input-text">
              Email:
            </label>
            <br />
            <input type="text" className="input" id="Ename" name="Ename" />
          </form>

          <br />
          <button type="submit" className="buttomSubmit">
            Submit
          </button>
        </div>
      </section>
    </>
  );
};
